@font-face {
    font-family: 'text-regular';
    font-style: normal;
    font-weight: 400;
    src: url("../fonts/text-regular.woff") format("woff");
    font-display: swap;
}

@font-face {
    font-family: 'text-medium';
    font-style: normal;
    font-weight: 500;
    src: url("../fonts/text-medium.woff") format("woff");
    font-display: swap;
}

@font-face {
    font-family: "text-black";
    font-style: normal;
    font-weight: 900;
    src: url("../fonts/text-black.woff") format("woff");
    font-display: swap;
}

:root {
    --color-font: #21201F;
}

body {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'text-medium';
    color: var(--color-font);
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'text-black';
}

h2 {
    font-size: 32px;
    letter-spacing: -0.6px;
    line-height: 32px;;
}


/* Баннер */
.main-header {
    margin-bottom: 34px;
}

.main-header_background {
    background: linear-gradient(rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 1)),
    linear-gradient(90deg, rgba(255, 92, 77, 1), rgba(235, 70, 159, 1), rgba(131, 65, 239, 1), rgba(63, 104, 249, 1));
}

.main-header__wrapper {
    padding: 32px 32px 0;
    margin-bottom: -6px;
}

.main-header__logo-yandex {
    display: block;
    margin-bottom: 27.6px;
}

.main-header h1 {
    font-weight: 900;
    font-size: 36px;
    line-height: 36px;
    margin: 0;
    margin-bottom: 25px;
    letter-spacing: -0.7px;
}

.main-header__text {
    font-weight: 500;
    font-size: 18px;
    margin: 0;
    line-height: 23.4px;
    position: relative;
}

.main-header__price {
    display: inline-flex;
    align-items: center;
    background: radial-gradient(rgba(241, 241, 241, 0.6), rgba(241, 241, 241, 0.6)), radial-gradient(#E0B8FF, #B6B9FF);
    border-radius: 500px;
    padding-right: 5px;
    height: 23.3px;
    position: absolute;
    bottom: 1.5px;
}

.main-header__new-price {
    display: block;
    background: linear-gradient(40deg, rgba(182, 185, 255, 1), rgba(224, 184, 255, 1));
    border-radius: 500px;
    padding-left: 7px;
    width: 49px;
    height: 23px;
    line-height: 21.6px;
}

.main-header__old-price {
    display: block;
    margin-left: 4px;
    font-size: 13px;
    color: #808080;
}

.main-header__image {
    padding-left: 5px;
    pointer-events: none;
}

.main-header__button {
    display: flex;
    justify-content: center;
    padding: 16px 72px 12px;
    margin: 0 auto;
    z-index: 2;
}

.main-header__button-wrapper {
    width: 100%;
    height: 49px;
    position: fixed;
    bottom: 26px;
    z-index: 2;
}


/* Секция с привилегиями */
.advantages {
    padding: 0 32px;
    margin-bottom: 66.7px;
}

.advantages h2 {
    margin: 0;
}

.advantages__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    padding-top: 26px;
}

.advantages__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 11px;
    min-height: 83px;
}

.advantages__item:not(:last-child) {
    margin-bottom: 24px;
}

.advantages__item img {
    width: 61px;
    height: 47px;
}

.advantages__item:nth-child(2) img {
    width: 89px;
    height: 83px;
    position: relative;
    bottom: 0;
    left: -13px;
}

.advantages__text {
    display: flex;
    align-items: center;
    width: 212px;
    margin-left: 27px;
    line-height: 19.2px;
}

.advantages__item:nth-child(2) .advantages__text {
    margin-left: 0;
}


/* Секция с эксклюзивными событиями*/

.exclusive {
    margin-bottom: 68.7px;
}

.exclusive__wrapper {
    padding: 0 32px;
    margin-bottom: 42.3px;
}

.exclusive__wrapper h2 {
    margin: 0;
    margin-bottom: 23px;
}

.exclusive__text {
    margin: 0;
    line-height: 19.2px;
}

.exclusive__gallery {
    width: 343px;
    margin: 0 auto;
}


/* Фото галерея*/

.photo-gallery__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    margin-bottom: 24px;
    position: relative;
}

.photo-gallery__list::after {
    content: url("../img/gallery-icon.png");
    position: absolute;
    top: -80px;
    right: -6px;
}

.photo-gallery__item {
    display: none;
}

.photo-gallery__item--active {
    display: block;
}

.photo-gallery__image {
    display: block;
    border-radius: 48px;
    pointer-events: none;
}

.photo-gallery__carusel-button {
    width: 108px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}

.photo-gallery__button {
    background-color: #FFDE40;
    border-radius: 50%;
    border: none;
    padding: 0;
    width: 48px;
    height: 48px;
    cursor: pointer;
}

.photo-gallery__button:disabled {
    opacity: 30%;
}

.photo-gallery__button:hover {
    background-color: #e9ca31;
}


/* Секция про абонемент */

.accede h2 {
    margin: 0;
    margin-bottom: 22px;
}

.accede__wrapper {
    padding: 0 32px;
    margin-bottom: 4px;
}

.accede__text {
    margin: 0;
    margin-bottom: 30px;
}

.accede__wrapper-image {
    display: none;
}



/* Секция про экономию */

.subscription {
    padding: 0 32px;
    margin-bottom: 34px;
    position: relative;
    top: -36px;
    display: flex;
    flex-direction: column-reverse;
}

.subscription h2 {
    margin: 0;
    margin-bottom: 22px;
}

.subscription__text {
    margin: 0;
    margin-bottom: 27px;
    line-height: 19.2px;
}

.subscription__button {
    padding: 14px 87.5px;
    filter: drop-shadow(0 4px 32px rgba(255, 222, 64, 0.59));
}

.subscription img {
    position: relative;
    top: 25px;
    left: -7px;
    margin-bottom: 38px;
    pointer-events: none;
}


/* Футер */
.main-footer {
    padding: 0 30px 72px;
}

.main-footer__text {
    font-size: 10px;
    color: #C5C5C5;
    margin: 0;
}




/* Кнопки */
.button {
    background-color:#FFDE40;
    border: none;
    border-radius: 36px;
    color: var(--color-font);
    font-size: 18px;
    font-weight: 500;
    line-height: 22px;
}

.button:hover {
    background-color: #ffd500;
}




/* Стили для десктопной версии */

@media (min-width: 1000px) {
    .main-header {
        position: relative;
    }

    .main-header__wrapper {
        width: 917px;
        margin: 0 auto;
        margin-bottom: 37px;
        padding-top: 124.94px;
    }

    .main-header h1 {
        font-size: 72px;
        line-height: 72px;
        letter-spacing: -1.4px;
        margin-bottom: 29px;
        position: relative;
        z-index: 2;
    }

    .main-header__logo-yandex {
        width: 176px;
        margin-bottom: 64px;
    }

    .main-header__text {
        width: 420px;
        font-size: 24px;
        line-height: 31.2px;
    }

    .main-header__price {
        width: 143px;
        height: 33px;
        bottom: -1px;
    }

    .main-header__new-price {
        width: 67px;
        height: 28px;
        padding-left: 12px;
        padding-top: 5px;
    }

    .main-header__old-price {
        font-size: 16px;
        margin-left: 12px;
    }

    .main-header__image {
        display: block;
        padding: 0;
        position: absolute;
        right: 76px;
        top: 42px;
        z-index: 1;
    }

    .main-header__image-wrapper {
        position: absolute;
        width: 896px;
        height: 766px;
        top: 0;
        right: 0;
    }

    .main-header__image-wrapper:after {
        content: url("../img/scooter-shadow.png");
        position: absolute;
        right: 100px;
        top: -15px;
    }

    .main-header__image-wrapper:before {
        content: url("../img/scooter-text.png");
        position: absolute;
        right: 460px;
        top: 395px;
        z-index: 5;
    }

    .main-header__button-wrapper {
        position: relative;
        display: block;
        bottom: 0;
        width: 917px;
        margin: 0 auto;
    }

    .main-header__button {
        display: block;
        margin: 0;
        padding: 22px 48px;
        filter: drop-shadow(0 4px 32px rgba(255, 222, 64, 0.59));
    }

    .button {
        font-size: 24px;
    }

    .advantages {
        width: 918px;
        margin: 0 auto;
        padding: 0;
        padding-top: 102px;
        margin-bottom: 121px;
    }

    .advantages h2 {
        width: 780px;
        font-size: 56px;
        line-height: 56px;
        letter-spacing: -1.1px;
    }

    .advantages__list {
        display: grid;
        grid-template-columns: 362px 400px;
        grid-column-gap: 112px;
        grid-row-gap: 61px;
        padding-top: 40px;
    }

    .advantages__item {
        display: block;
        padding: 0;
    }

    .advantages__item:not(:last-child) {
        margin-bottom: 0;
    }

    .advantages__item img {
        width: 83px;
        height: 83px;
        margin-bottom: 13px;
    }

    
    .advantages__item:nth-child(2) img {
        width: 93.98px;
        height: 83px;
        position: relative;
        left: 0;
    }

    .advantages__text {
        width: 100%;
        margin-left: 0;
        font-size: 24px;
        line-height: 28.8px;
    }


    .exclusive {
        margin-bottom: 181px;
    }

    .exclusive h2 {
        font-size: 48px;
        line-height: 48px;
        letter-spacing: -1px;
        margin-bottom: 35px;
    }

    .exclusive__wrapper {
        width: 917px;
        margin: 0 auto;
    }

    .exclusive__text {
        width: 440px;
        font-size: 24px;
        line-height: 28.8px;
    }


    .exclusive__gallery {
        width: 934px;
        margin: 0 auto;
    }

    .photo-gallery__list {
        display: grid;
        grid-template-columns: 455px 455px;
        grid-column-gap: 24px;
        grid-row-gap: 23px;
        rotate: 2deg;
        top: -146px;
        left: 3px;
    }

    .photo-gallery__list::after {
        content: '';
    }

    .photo-gallery__item {
        display: block;
    }

    .photo-gallery__item:nth-child(1) {
        position: relative;
        top: 227px;
    }

    .photo-gallery__item:nth-child(3) {
        position: relative;
        top: 227px;
    }

    .photo-gallery__item:nth-child(1)::after {
        content: url("../img/position-icon.png");
        position: absolute;
        top: -130px;
        left: -260px;
    }

    .photo-gallery__item:nth-child(2)::after {
        content: url("../img/gallery-icon-desktop.png");
        position: absolute;
        top: -163px;
        right: -155px;
    }

    .photo-gallery__item:nth-child(4)::after {
        content: url("../img/drink-icon.png");
        position: absolute;
        top: 605px;
        right: -262px;
        rotate: -2deg;
    }

    .photo-gallery__image {
        width: 455px;
        height: 455px;
        border-radius: 64px;
    }


    .photo-gallery__carusel-button {
        display: none;
    }



    .accede {
        width: 917px;
        margin: 0 auto;
        display: flex;
        height: 463px;
        margin-bottom: 101px;
    }

    .accede__wrapper {
        padding: 0;
        height: 463px;
    }

    .accede h2 {
        width: 407px;
        font-size: 48px;
        line-height: 48px;
        letter-spacing: -1px;
        margin-bottom: 34px;
    }

    .accede__text {
        width: 419px;
        font-size: 24px;
        line-height: 28.8px;
    }

    .accede img {
        display: block;
        position: relative;
        top: -34px;
        right: -100px;
        z-index: 1;
    }

    .accede__wrapper-image {
        display: block;
        position: relative;
    }

    .accede__wrapper-image img {
        pointer-events: none;
    }

    .accede__wrapper-image::after {
        content: url("../img/shadow.png");
        position: absolute;
        top: -260px;
    }

    .accede__wrapper-image::before {
        content: url("../img/plus.png");
        position: absolute;
        top: 54px;
        left: 53px;
        z-index: 2;
    }



    .subscription {
        top: 0;
        display: flex;
        flex-direction: row-reverse;
        width: 917px;
        height: 565px;
        margin: 0 auto;
        margin-bottom: 44px;
        padding: 0;
    }

    .subscription__wrapper {
        width: 419px;
        position: relative;
        left: -23px;
    }

    .subscription h2 {
        font-size: 48px;
        line-height: 48px;
        letter-spacing: -1px;
        margin-bottom: 35px;
    }

    .subscription img {
        width: 490px;
        height: 495px;
        position: relative;
        left: -103px;
        top: -32px; 
    }

    .subscription__text {
        width: 419px;
        font-size: 24px;
        line-height: 28.8px;
        margin-bottom: 40px;
    }


    .subscription__button {
        padding: 23px 49px;
    }

    .main-footer {
        width: 906px;
        margin: 0 auto;
        padding: 0 35px 72px 22px;
    }

    .main-footer__text {
        font-size: 13px;
        line-height: 15.6px;
    }
    

}

